import 'ol/ol.css';
import Map from 'ol/Map';
import OSM from 'ol/source/OSM';

import TileLayer from 'ol/layer/Tile';
import View from 'ol/View';


var map1 = new Map({
  layers: [
    new TileLayer({
      source: new OSM(),
    })],
  target: 'map1',
  view: new View({
    projection: 'EPSG:3857',
    center: [1076233.358255, 6878109.553213],
    zoom: 4,
  }),
});
var map2 = new Map({
  layers: [
    new TileLayer({
      source: new OSM(),
    })],
  target: 'map2',
  view: new View({
    projection: 'EPSG:3857',
    center: [1917652.165619, 6036690.745850],
    zoom: 9,
  }),
});
